.apps {
    background-color: #FFF8E4;
    padding: 120px 60px 93px 60px;

    &__title {
        font-size: 50px;
        font-weight: 600;
        line-height: 60px;
        text-align: center;
    }

    &__wraper__up {
        display: flex;
        gap: 110px;
        margin: 58px 0 58px 0;
    }

    &__wraper__down {
        display: flex;
        gap: 110px;
    }

    &__article__title {
        font-size: 25px;
        font-weight: 600;
        line-height: 35px;
    }

    &__pharahraph {
        font-size: 18px;
        font-weight: 400;
        line-height: 25px;
    }
}